import {
    get,
    post
} from '@/utils/axiosConfig/axios'
import {
    CENTERED_ALIGNMENT
} from 'element-plus/es/components/virtual-list/src/defaults'

export function setuserActiveConfig(params1, parmas2) {
    // 根据params，请求获取渲染图标的数据，静态数据调试把他注释了
    return new Promise((resolve, reject) => {

        // 模拟用户偏好和兴趣数据
        var userPreferenceData = [{
                value: 30,
                name: '产品类别A'
            },
            {
                value: 25,
                name: '产品类别B'
            },
            {
                value: 20,
                name: '产品类别C'
            },
            {
                value: 15,
                name: '产品类别D'
            },
            {
                value: 10,
                name: '产品类别E'
            }
        ];

        // 模拟用户活跃度数据
        var userActivityData = [{
                value: 70,
                name: '高活跃用户'
            },
            {
                value: 30,
                name: '低活跃用户'
            }
        ];

        // 配置图表
        var option = {
            title: [{
                    text: '用户偏好和兴趣',
                    left: 'center',
                    top: '10%',
                    textStyle: {
                        fontWeight: 'normal',
                        fontSize: 18
                    }
                },
                {
                    text: '用户活跃度',
                    left: 'center',
                    top: '60%',
                    textStyle: {
                        fontWeight: 'normal',
                        fontSize: 18
                    }
                }
            ],
            series: [{
                    type: 'pie',
                    radius: ['30%', '60%'],
                    center: ['50%', '30%'],
                    data: userPreferenceData,
                    label: {
                        show: true,
                        position: 'outside',
                        formatter: '{b}: {c} ({d}%)'
                    }
                },
                {
                    type: 'pie',
                    radius: ['30%', '60%'],
                    center: ['50%', '80%'],
                    data: userActivityData,
                    label: {
                        show: true,
                        position: 'outside',
                        formatter: '{b}: {c} ({d}%)'
                    }
                }
            ]
        };

        // get({
        //     url: url,
        //     params: {
        //         params1: params1,
        //         parmas2: parmas2
        //     }
        // }).then(data => {
        //     // data为可视化数据
        resolve(option)
        // }).catch(err=>{
        //     reject(err)
        // })
    })
}

export default setuserActiveConfig