import {
    get,
    post
} from '@/utils/axiosConfig/axios'
import {
    CENTERED_ALIGNMENT
} from 'element-plus/es/components/virtual-list/src/defaults'

export function setteamreserveConfig(params1, parmas2) {
    // 根据params，请求获取渲染图标的数据，静态数据调试把他注释了
    return new Promise((resolve, reject) => {
        // 模拟的数据，不同用户的留存率和转化率
        var data = [{
                user: '用户A',
                retentionRate: 0.6,
                conversionRate: 0.2
            },
            {
                user: '用户B',
                retentionRate: 0.4,
                conversionRate: 0.3
            },
            {
                user: '用户C',
                retentionRate: 0.8,
                conversionRate: 0.4
            },
            {
                user: '用户D',
                retentionRate: 0.5,
                conversionRate: 0.1
            },
            {
                user: '用户E',
                retentionRate: 0.7,
                conversionRate: 0.3
            }
        ];

        // 提取用户、留存率和转化率数据
        var userData = data.map(item => item.user);
        var retentionRateData = data.map(item => item.retentionRate);
        var conversionRateData = data.map(item => item.conversionRate);

        // 配置图表选项
        var option = {
            title: {
                text: '电商用户购买留存率和转化率情况图',
                left: 'center',
                top: '90%'
            },
            tooltip: {},
            legend: {
                data: ['留存率', '转化率']
            },
            xAxis: {
                type: 'category',
                data: userData
            },
            yAxis: [{
                    type: 'value',
                    name: '留存率',
                    axisLabel: {
                        formatter: '{value}%'
                    }
                },
                {
                    type: 'value',
                    name: '转化率',
                    axisLabel: {
                        formatter: '{value}%'
                    }
                }
            ],
            series: [{
                    name: '留存率',
                    type: 'bar',
                    data: retentionRateData.map(rate => rate * 100) // 转换为百分比
                },
                {
                    name: '转化率',
                    type: 'line',
                    yAxisIndex: 1,
                    data: conversionRateData.map(rate => rate * 100) // 转换为百分比
                }
            ]
        };

        // get({
        //     url: url,
        //     params: {
        //         params1: params1,
        //         parmas2: parmas2
        //     }
        // }).then(data => {
        //     // data为可视化数据
        resolve(option)
        // }).catch(err=>{
        //     reject(err)
        // })
    })
}

export default setteamreserveConfig