import {
    get,
    post
} from '@/utils/axiosConfig/axios'
import {
    CENTERED_ALIGNMENT
} from 'element-plus/es/components/virtual-list/src/defaults'

export function setuserhobbyConfig(params1, parmas2) {
    // 根据params，请求获取渲染图标的数据，静态数据调试把他注释了
    return new Promise((resolve, reject) => {
        // 模拟数据
        var data = [
            ['电子产品', '手机', 500],
            ['电子产品', '电视', 800],
            ['服饰鞋包', '鞋子', 300],
            ['服饰鞋包', '衣服', 200],
            ['家居生活', '家具', 600],
            ['家居生活', '家电', 400],
            ['食品饮料', '零食', 350],
            ['食品饮料', '饮料', 250],
            ['运动健康', '运动装备', 700],
            ['运动健康', '健身器材', 450]
        ];

        // 配置项
        var option = {
            title: {
                text: '用户购买品类偏好',
                left: 'center'
            },
            tooltip: {
                position: 'top'
            },
            grid: {
                height: '50%',
                top: '20%',
                left: '60px',
            },
            xAxis: {
                type: 'category',
                data: data.map(item => item[0]),
                splitArea: {
                    show: true
                }
            },
            yAxis: {
                type: 'category',
                data: data.map(item => item[1]),
                splitArea: {
                    show: true
                }
            },
            visualMap: {
                min: 0,
                max: 1000,
                calculable: true,
                orient: 'horizontal',
                left: 'center',
                bottom: '2%'
            },
            series: [{
                name: '购买次数',
                type: 'heatmap',
                data: data.map(item => [item[0], item[1], item[2]]),
                label: {
                    show: true
                },
                emphasis: {
                    itemStyle: {
                        shadowBlur: 10,
                        shadowColor: 'rgba(0, 0, 0, 0.5)'
                    }
                }
            }]
        };




        // get({
        //     url: url,
        //     params: {
        //         params1: params1,
        //         parmas2: parmas2
        //     }
        // }).then(data => {
        //     // data为可视化数据
        resolve(option)
        // }).catch(err=>{
        //     reject(err)
        // })
    })
}

export default setuserhobbyConfig