import {
    get,
    post
} from '@/utils/axiosConfig/axios'
import {
    CENTERED_ALIGNMENT
} from 'element-plus/es/components/virtual-list/src/defaults'

export function setuserorderConfig(params1, parmas2) {
    // 根据params，请求获取渲染图标的数据，静态数据调试把他注释了
    return new Promise((resolve, reject) => {
        // 模拟数据
        var data = [{
                segment: '0-100元',
                value: [80, 50, 70, 30, 60]
            },
            {
                segment: '100-500元',
                value: [50, 70, 60, 80, 40]
            },
            {
                segment: '500-1000元',
                value: [40, 60, 70, 50, 80]
            },
            {
                segment: '1000元以上',
                value: [70, 40, 80, 60, 50]
            }
        ];

        // 配置项
        var option = {
            title: {
                text: '用户客单价分布',
                left: 'center'
            },
            tooltip: {},
            radar: {
                indicator: [{
                        name: '购买频率',
                        max: 100
                    },
                    {
                        name: '购买金额',
                        max: 100
                    },
                    {
                        name: '购买间隔',
                        max: 100
                    },
                    {
                        name: '浏览行为',
                        max: 100
                    },
                    {
                        name: '购买品类偏好',
                        max: 100
                    }
                ]
            },
            series: [{
                name: '用户数量',
                type: 'radar',
                data: data.map(item => ({
                    name: item.segment,
                    value: item.value
                }))
            }]
        };



        // get({
        //     url: url,
        //     params: {
        //         params1: params1,
        //         parmas2: parmas2
        //     }
        // }).then(data => {
        //     // data为可视化数据
        resolve(option)
        // }).catch(err=>{
        //     reject(err)
        // })
    })
}

export default setuserorderConfig