import {
    get,
    post
} from '@/utils/axiosConfig/axios'
import {
    CENTERED_ALIGNMENT
} from 'element-plus/es/components/virtual-list/src/defaults'

export function setuserBuyConfig(params1, parmas2) {
    // 根据params，请求获取渲染图标的数据，静态数据调试把他注释了
    return new Promise((resolve, reject) => {
        // 模拟数据
        var data = [{
                frequency: '0-1次',
                count: 500
            },
            {
                frequency: '2-5次',
                count: 800
            },
            {
                frequency: '6-10次',
                count: 300
            },
            {
                frequency: '10次以上',
                count: 200
            }
        ];

        // 配置项
        var option = {
            title: {
                text: '用户购买频率分布',
                left: 'center'
            },
            tooltip: {},
            xAxis: {
                type: 'category',
                data: data.map(item => item.frequency)
            },
            yAxis: {},
            series: [{
                name: '用户数量',
                type: 'bar',
                data: data.map(item => item.count)
            }]
        };

        // get({
        //     url: url,
        //     params: {
        //         params1: params1,
        //         parmas2: parmas2
        //     }
        // }).then(data => {
        //     // data为可视化数据
        resolve(option)
        // }).catch(err=>{
        //     reject(err)
        // })
    })
}

export default setuserBuyConfig