import {
    get,
    post
} from '@/utils/axiosConfig/axios'
import {
    CENTERED_ALIGNMENT
} from 'element-plus/es/components/virtual-list/src/defaults'

export function setuserActionConfig(params1, parmas2) {
    // 根据params，请求获取渲染图标的数据，静态数据调试把他注释了
    return new Promise((resolve, reject) => {
        var channelData = [{
                channel: '网站',
                purchaseCount: 100,
                purchaseAmount: 5000
            },
            {
                channel: '移动应用',
                purchaseCount: 80,
                purchaseAmount: 4000
            },
            {
                channel: '社交媒体',
                purchaseCount: 50,
                purchaseAmount: 3000
            },
            // 更多数据...
        ];

        // 配置购买渠道偏好图表
        var option = {
            title: {
                text: '购买渠道偏好',
                left: 'center'
            },
            tooltip: {
                trigger: 'axis'
            },
            xAxis: {
                type: 'category',
                data: channelData.map(item => item.channel)
            },
            yAxis: [{
                    type: 'value',
                    name: '购买次数'
                },
                {
                    type: 'value',
                    name: '购买金额'
                }
            ],
            series: [{
                    name: '购买次数',
                    type: 'bar',
                    data: channelData.map(item => item.purchaseCount)
                },
                {
                    name: '购买金额',
                    type: 'bar',
                    yAxisIndex: 1,
                    data: channelData.map(item => item.purchaseAmount)
                }
            ]
        };


        // get({
        //     url: url,
        //     params: {
        //         params1: params1,
        //         parmas2: parmas2
        //     }
        // }).then(data => {
        //     // data为可视化数据
        resolve(option)
        // }).catch(err=>{
        //     reject(err)
        // })
    })
}

export default setuserActionConfig