import {
    get,
    post
} from '@/utils/axiosConfig/axios'
import {
    CENTERED_ALIGNMENT
} from 'element-plus/es/components/virtual-list/src/defaults'

export function setusercheapConfig(params1, parmas2) {
    // 根据params，请求获取渲染图标的数据，静态数据调试把他注释了
    return new Promise((resolve, reject) => {
        // 个性化折扣与优惠数据示例
        var personalizedDiscountData = [{
                value: 35,
                name: '5%折扣'
            },
            {
                value: 30,
                name: '10%折扣'
            },
            {
                value: 25,
                name: '15%折扣'
            },
            {
                value: 20,
                name: '20%折扣'
            },
            {
                value: 15,
                name: '特别优惠'
            }
        ];
        var option = {
            title: {
                text: '个性化折扣与优惠',
                x: 'center'
            },
            tooltip: {
                trigger: 'item',
                formatter: '{b}: {c} ({d}%)'
            },
            series: [{
                type: 'pie',
                radius: '60%',
                center: ['50%', '50%'],
                data: personalizedDiscountData,
                emphasis: {
                    itemStyle: {
                        shadowBlur: 10,
                        shadowOffsetX: 0,
                        shadowColor: 'rgba(0, 0, 0, 0.5)'
                    }
                }
            }]
        };
        // get({
        //     url: url,
        //     params: {
        //         params1: params1,
        //         parmas2: parmas2
        //     }
        // }).then(data => {
        //     // data为可视化数据
        resolve(option)
        // }).catch(err=>{
        //     reject(err)
        // })
    })
}

export default setusercheapConfig