import {
    get,
    post
} from '@/utils/axiosConfig/axios'
import {
    CENTERED_ALIGNMENT
} from 'element-plus/es/components/virtual-list/src/defaults'

export function setteamConfig(params1, parmas2) {
    // 根据params，请求获取渲染图标的数据，静态数据调试把他注释了
    return new Promise((resolve, reject) => {
        // 模拟的数据，按照不同时间段的用户消费金额
        var data = [{
                time: '2023-01',
                amount: 5000
            },
            {
                time: '2023-02',
                amount: 8000
            },
            {
                time: '2023-03',
                amount: 6000
            },
            {
                time: '2023-04',
                amount: 10000
            },
            {
                time: '2023-05',
                amount: 7500
            }
        ];

        // 提取时间和消费金额数据
        var timeData = data.map(item => item.time);
        var amountData = data.map(item => item.amount);

        // 配置图表选项
        var option = {
            title: {
                text: '电商用户购买消费情况图'
            },
            tooltip: {},
            xAxis: {
                data: timeData
            },
            grid: {
                left: "20%",


            },
            yAxis: {},
            series: [{
                name: '消费金额',
                type: 'bar',
                data: amountData
            }]
        };
        // get({
        //     url: url,
        //     params: {
        //         params1: params1,
        //         parmas2: parmas2
        //     }
        // }).then(data => {
        //     // data为可视化数据
        resolve(option)
        // }).catch(err=>{
        //     reject(err)
        // })
    })
}

export default setteamConfig