import {
    get,
    post
} from '@/utils/axiosConfig/axios'
import {
    CENTERED_ALIGNMENT
} from 'element-plus/es/components/virtual-list/src/defaults'

export function setnorthStarConfig(params1, parmas2) {
    // 根据params，请求获取渲染图标的数据，静态数据调试把他注释了
    return new Promise((resolve, reject) => {
        // 指定图表的配置项和数据
        var option = {
            title: {
                text: '北极星指标动画效果图表',
                left: 'center',
                subtext: '示例数据'
            },
            tooltip: {},
            legend: {
                data: ['指标值']
            },
            polar: {
                radius: '70%'
            },
            angleAxis: {
                type: 'category',
                data: ['首次购买人数', '第二次购买人数', '第三次购买人数', '北极星客户指标']
            },
            radiusAxis: {},
            series: [{
                type: 'bar',
                data: [90, 70, 50, 40],
                coordinateSystem: 'polar',
                animationDuration: 2000, // 设置动画时长为 2 秒
                animationEasing: 'cubicInOut', // 设置动画缓动效果
                animationDelay: function (idx) { // 为每个数据项设置动画延迟时间，以实现逐个出现的效果
                    return idx * 200;
                }
            }]
        };


        // get({
        //     url: url,
        //     params: {
        //         params1: params1,
        //         parmas2: parmas2
        //     }
        // }).then(data => {
        //     // data为可视化数据
        resolve(option)
        // }).catch(err=>{
        //     reject(err)
        // })
    })
}

export default setnorthStarConfig