import {
    get,
    post
} from '@/utils/axiosConfig/axios'
import {
    CENTERED_ALIGNMENT
} from 'element-plus/es/components/virtual-list/src/defaults'

export function setmanyValueConfig(params1, parmas2) {
    // 根据params，请求获取渲染图标的数据，静态数据调试把他注释了
    return new Promise((resolve, reject) => {
        // 生成随机数据函数
        function generateRandomData(num) {
            var data = [];
            for (var i = 0; i < num; i++) {
                data.push(Math.random() * 100);
            }
            return data;
        }

        // 指定图表的配置项和数据
        var option = {
            title: {
                text: 'OSM多指标图表',
                left: 'center',
                bottom: '10%'
            },
            tooltip: {
                trigger: 'axis',
                axisPointer: {
                    type: 'shadow'
                }
            },
            legend: {
                data: ['停留时长', '跳出率', '模块点击率', '转化率']
            },
            grid: {
                top: '20%',
                left: '3%',
                right: '4%',
                bottom: '30%',
                containLabel: true
            },
            xAxis: [{
                type: 'category',
                data: ['模块一', '模块二', '模块三', '模块四', '模块五']
            }],
            yAxis: [{
                    type: 'value',
                    name: '停留时长',
                    axisLabel: {
                        formatter: '{value} 秒'
                    }
                },
                {
                    type: 'value',
                    name: '跳出率/模块点击率/转化率',
                    axisLabel: {
                        formatter: '{value} %'
                    }
                }
            ],
            series: [{
                    name: '停留时长',
                    type: 'bar',
                    data: generateRandomData(5),
                    animationDuration: 1000,
                    animationDelay: function (idx) {
                        return idx * 200;
                    }
                },
                {
                    name: '跳出率',
                    type: 'line',
                    yAxisIndex: 1,
                    data: generateRandomData(5),
                    animationDuration: 1000,
                    animationDelay: function (idx) {
                        return idx * 200;
                    }
                },
                {
                    name: '模块点击率',
                    type: 'line',
                    yAxisIndex: 1,
                    data: generateRandomData(5),
                    animationDuration: 1000,
                    animationDelay: function (idx) {
                        return idx * 200;
                    }
                },
                {
                    name: '转化率',
                    type: 'line',
                    yAxisIndex: 1,
                    data: generateRandomData(5),
                    animationDuration: 1000,
                    animationDelay: function (idx) {
                        return idx * 200;
                    }
                }
            ]
        };


        // get({
        //     url: url,
        //     params: {
        //         params1: params1,
        //         parmas2: parmas2
        //     }
        // }).then(data => {
        //     // data为可视化数据
        resolve(option)
        // }).catch(err=>{
        //     reject(err)
        // })
    })
}

export default setmanyValueConfig