import {
    get,
    post
} from '@/utils/axiosConfig/axios'
import {
    CENTERED_ALIGNMENT
} from 'element-plus/es/components/virtual-list/src/defaults'

export function setgiveupConfig(params1, parmas2) {
    // 根据params，请求获取渲染图标的数据，静态数据调试把他注释了
    return new Promise((resolve, reject) => {

        // 模拟的数据，不同用户的购物篮放弃率
        var data = [{
                user: '用户A',
                cartAbandonmentRate: 0.3
            },
            {
                user: '用户B',
                cartAbandonmentRate: 0.5
            },
            {
                user: '用户C',
                cartAbandonmentRate: 0.2
            },
            {
                user: '用户D',
                cartAbandonmentRate: 0.4
            },
            {
                user: '用户E',
                cartAbandonmentRate: 0.6
            }
        ];

        // 提取用户和购物篮放弃率数据
        var userData = data.map(item => item.user);
        var cartAbandonmentRateData = data.map(item => ({
            name: item.user,
            value: item.cartAbandonmentRate * 100 // 转换为百分比
        }));

        // 配置图表选项
        var option = {
            title: {
                text: '电商用户购物篮放弃率饼图'
            },
            tooltip: {
                trigger: 'item',
                formatter: '{a} <br/>{b}: {c}%'
            },
            series: [{
                name: '购物篮放弃率',
                type: 'pie',
                radius: '50%',
                center: ['50%', '50%'],
                data: cartAbandonmentRateData,
                label: {
                    formatter: '{b}: {c}%'
                },
                emphasis: {
                    label: {
                        show: true,
                        fontSize: '14',
                        fontWeight: 'bold'
                    }
                }
            }]
        };

        // get({
        //     url: url,
        //     params: {
        //         params1: params1,
        //         parmas2: parmas2
        //     }
        // }).then(data => {
        //     // data为可视化数据
        resolve(option)
        // }).catch(err=>{
        //     reject(err)
        // })
    })
}

export default setgiveupConfig