import {
    get,
    post
} from '@/utils/axiosConfig/axios'
import {
    CENTERED_ALIGNMENT
} from 'element-plus/es/components/virtual-list/src/defaults'

export function setaverageConfig(params1, parmas2) {
    // 根据params，请求获取渲染图标的数据，静态数据调试把他注释了
    return new Promise((resolve, reject) => {
        // 模拟的数据，不同用户的购买客单价情况
        var data = [{
                user: '用户A',
                averageOrderValue: 50,
                orderCount: 10
            },
            {
                user: '用户B',
                averageOrderValue: 80,
                orderCount: 5
            },
            {
                user: '用户C',
                averageOrderValue: 120,
                orderCount: 8
            },
            {
                user: '用户D',
                averageOrderValue: 70,
                orderCount: 12
            },
            {
                user: '用户E',
                averageOrderValue: 90,
                orderCount: 6
            }
        ];

        // 提取用户、客单价和订单数量数据
        var userData = data.map(item => item.user);
        var averageOrderValueData = data.map(item => item.averageOrderValue);
        var orderCountData = data.map(item => item.orderCount);

        // 配置图表选项
        var option = {
            title: {
                text: '电商用户购买客单价散点气泡图'
            },
            xAxis: {
                type: 'value',
                name: '客单价'
            },
            yAxis: {
                type: 'value',
                name: '订单数量'

            },
            tooltip: {
                show: true
            },
            grid: {},
            series: [{
                data: data.map(item => ({
                    name: item.user,
                    value: [item.averageOrderValue, item.orderCount, item.averageOrderValue * 5] // 使用气泡的大小表示客单价
                })),
                type: 'scatter',
                symbolSize: function (data) {
                    return Math.sqrt(data[2]); // 设置气泡的大小
                },
                label: {
                    formatter: function (param) {
                        return param.data.name; // 显示用户名称
                    },
                    position: 'top'
                }
            }]
        };

        // get({
        //     url: url,
        //     params: {
        //         params1: params1,
        //         parmas2: parmas2
        //     }
        // }).then(data => {
        //     // data为可视化数据
        resolve(option)
        // }).catch(err=>{
        //     reject(err)
        // })
    })
}

export default setaverageConfig